.explore-page {

    .nav {
        position: relative;
        width: 100%;
        list-style-type: none;
        padding: 30px 0;

        &__wrapper {
            position: fixed;
            right: 0;
            top: 40%;
            width: 200px;
            z-index: 7; }

        &::after {
            content: '';
            position: absolute;
            top: 0;
            bottom: 0;
            right: 40px;
            height: 100%;
            width: 1px;
            background: #ffffff55; }

        &__item {
            position: relative;
            color: #ffffff;
            width: 100%;
            padding: 10px 0;
            padding-right: 80px;
            text-align: right;
            font-size: 0.9em;
            cursor: pointer;

            span {
                display: block;
                padding: 5px 10px;
                width: 100%;
                height: 100%; }

            &::after {
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                right: 30px;
                width: 20px;
                background: url('/decorations/inactive-city.png') no-repeat center; }

            &--active {

                span {
                    background: #ffffff;
                    color: #111111; }

                &::after {
                    background: url('/decorations/char-desc-emblem.png') no-repeat center; } }

            &--inactive {
                color: #bbbbbb;
                cursor: initial; } } }


    .area {
        min-height: 100vh; }

    .teyvat {
        background: url('/explore/1.png') no-repeat center center / cover;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 20px clamp(10px, 20vw, 300px);

        &__title {
            color: #ffffff;
            font-size: 5em;
            font-weight: 400; }

        &__frill {
            margin: 20px 0; }

        &__text {
            color: #ffffff;
            text-align: center;
            line-height: 2em; }

        &__hint {
            position: relative;
            background: url('/decorations/hint-bg2.png') no-repeat center center / cover;
            height: 51px;
            width: 51px;

            &__wrapper {
                position: absolute;
                bottom: 50px; }

            &::before {
                position: absolute;
                top: -50%;
                left: -50%;
                content: '';
                height: 100px;
                width: 100px;
                transform-origin: center;
                background: url('/decorations/hint-bg.png') no-repeat center center / cover;
                animation: wave 1s infinite alternate ease;
                transform-origin: center; }

            &__arrow {
                width: 22px;
                position: absolute;
                top: 30px;
                left: 15px;
                animation: hover 1.4s infinite ease; } } }


    .mondstadt {
        background: url('/explore/2.jpg') no-repeat center center / cover; }

    .liyue {
        background: url('/explore/3.jpg') no-repeat center center / cover; }

    .mondstadt,
    .liyue {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 20px clamp(10px, 30vw, 600px);
        padding-top: 20vh;

        &__emblem {
            height: 120px; }

        &__title {
            position: relative;
            color: #ffffff;
            font-size: 2.5em;
            font-weight: 300;

            $frillHeight: 15px;
            $halfFrillHeight: $frillHeight / 2;
            &::before {
                content: '';
                position: absolute;
                top: calc(50% - #{$halfFrillHeight});
                right: calc(100% + 50px);
                background: url('/decorations/frill.png') no-repeat right center/cover;
                width: 200px;
                height: $frillHeight; }
            &::after {
                content: '';
                position: absolute;
                top: calc(50% - #{$halfFrillHeight});
                left: calc(100% + 50px);
                background: url('/decorations/frill.png') no-repeat right center/cover;
                width: 200px;
                height: $frillHeight;
                transform: rotateZ(180deg); } }

        &__text {
            color: #ffffff;
            text-align: center;
            line-height: 1.8em;
            font-size: 0.9em;
            margin-top: 5vh; }

        .area__hint {
            position: relative;
            background: url('/decorations/hint2-bg2.png') no-repeat center center / cover;
            height: 50px;
            width: 50px;

            &__wrapper {
                margin-top: 10vh; }

            &::before {
                position: absolute;
                top: -50%;
                left: -50%;
                content: '';
                height: 100px;
                width: 100px;
                transform-origin: center;
                background: url('/decorations/hint2-bg.png') no-repeat center center / cover;
                animation: wave 1s infinite alternate ease;
                transform-origin: center; } } } }

@keyframes wave {
    0% {
        transform: scale(0.3);
        opacity: 1; }
    100% {
        transform: scale(1);
        opacity: 0.6; } }

@keyframes hover {
    0% {
        transform: translateY(0); }
    50% {
        transform: translateY(-5px); }
    100% {
        transform: translateY(0); } }

