.cities {

    .city {
        position: relative;
        height: 30vh;
        width: 100%;
        max-width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        cursor: pointer;
        box-sizing: border-box;
        transition: box-shadow 0.2s linear;

        &__bg {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            height: 100%;
            width: 100%;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
            background-blend-mode: darken;
            background-color: #00000055;
            transition: transform 0.5s; }

        &__girl {
            position: absolute;
            top: 0;
            bottom: 0;
            right: -200px;
            opacity: 0;
            z-index: 5;
            height: 100%;
            transition: all 0.3s ease; }

        &__title {
            position: relative;
            background: url('/cities/emblem.png') no-repeat center center;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            padding: 50px;

            span {
                color: #ffffff;
                font-size: 2.3em;
                letter-spacing: 2px;
                user-select: none; }

            &::after {
                content: '';
                height: 20px;
                background: url('/cities/emblem-shine.png') no-repeat center center;
                position: absolute;
                left: 0;
                right: 0;
                top: calc(50% + 30px);
                opacity: 0;
                transition: all 0.3s ease; } }

        &:hover {
            box-shadow: 0 0 12px #ffffff;

            .city__bg {
                background-color: #00000022;
                transform: scale(1.05); }

            .city__girl {
                opacity: 1 !important; }

            .city__title::after {
                opacity: 1; } }

        &--inactive {
            pointer-events: none;
            filter: brightness(0.6);

            .city__title {
                background: none !important;

                span {
                    color: #888888 !important;
                    font-size: 2em; } } } } }


