.hero {
    position: relative;
    height: 100vh;
    max-height: 100vh;
    width: 100%;
    max-width: 100vw;
    overflow-x: hidden;

    &__bg {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        height: 100%;
        min-width: 100%;
        overflow-x: hidden;
        object-fit: cover; }

    &__content {
        position: absolute;
        bottom: 10%;
        left: 0;
        right: 0;
        display: flex;
        flex-direction: column;
        align-items: center;

        >* {
            margin-bottom: 30px;

            &:last-child {
                margin-bottom: 0; } } }

    &__play {
        background: url('/buttons/hero-play.png') no-repeat center center;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        padding: 15px 150px;

        $side: 45px;
        &-btn {
            height: $side;
            width: $side;
            border: 0;
            border-radius: 50%;
            background: #fff;
            cursor: pointer;
            transition: 0.3s all ease;

            &-icon {
                color: #4b6572;
                font-size: 20px;
                margin-left: 5px; }

            &:hover {
                background: transparent; }

            &:hover &-icon {
                color: #fff; } } }
    &__text {
        font-size: 1.8em;
        color: #fff8f0;
        text-shadow: 0px 0px 5px #ee933f; }

    &__downloads {
        display: flex;
        flex-direction: row;
        align-items: center;

        &-btn {
            margin: 0 5px;
            width: 190px;
            height: 65px;
            border: 0;
            background: url('/buttons/platform-bg.png') no-repeat center center / cover;
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all 0.3s ease;

            &:hover {
                filter: brightness(1.05); } } }

    &__rating {
        position: absolute;
        bottom: 10px;
        left: 10px; } }
