.footer {
    width: 100%;
    max-width: 100%;
    background: #111111;
    display: flex;
    flex-direction: column;
    align-items: center;

    .top {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding: 0 20px;
        border-bottom: 1px solid #333333;
        width: 100%;
        max-width: 1200px;

        .social {
            display: flex;
            flex-direction: row;
            align-items: center;
            padding: 10px 0;

            &-link {
                margin-right: 30px; }

            &-icon {
                color: #999999;
                font-size: 1.5em; } }

        .language {
            display: flex;
            flex-direction: row;
            align-items: center;
            padding: 10px 0;

            &-icon {
                color: #999999;
                font-size: 1.5em; }

            &-selector {
                color: #999999;
                font-size: 1.2em;
                margin-left: 20px; } } }

    .body {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        padding: 40px 20px;
        width: 100%;
        max-width: 1000px;

        .left {
            flex: 1;
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            padding: 0 30px;

            >* {
                margin: 0 10px; }

            .brand-img {}

            .separator {
                height: 50px;
                width: 1px;
                background: #ffffff; } }

        .right {
            flex: 1;
            display: flex;
            flex-direction: column;

            >* {
                margin-bottom: 15px; }

            .footer-nav {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                align-items: center;

                a {
                    text-decoration: none;
                    color: #999999;
                    font-size: 0.9em;
                    margin-right: 10px;
                    cursor: pointer;

                    &::before {
                        content: '|';
                        margin-right: 10px; } } }

            .copyright {
                color: #ffffff;
                font-size: 0.9em; }

            .copyright-sub {
                color: #999999;
                font-size: 0.8em; }

            .footer-ratings {
                display: flex;
                flex-direction: row;
                align-items: start;

                .footer-rating {
                    margin-right: 10px; } } } } }
