.characters-page {
    position: relative;
    height: 100vh;
    min-height: 100vh;
    width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: row;
    align-items: stretch;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    overflow-x: hidden;

    .bg,
    .bg--prev {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        height: 100%;
        width: 100%; }

    .bg {
        z-index: 0; }

    .bg--prev {
        z-index: 1;
        opacity: 0; }

    .sprite {
        z-index: 3;
        position: absolute;
        top: 0;
        bottom: 0;
        left: clamp(0vw, 200px,50vw);
        height: 100%;
        object-fit: cover; }

    .shade {
        position: absolute;
        z-index: 2;
        top: 0;
        bottom: 0;
        right: 0;
        height: 100%;
        width: 100%;
        object-fit: cover; }

    .cities__list {
        z-index: 4;
        position: relative;
        width: 270px;
        max-width: 300px;
        list-style-type: none;
        padding-top: 22vh;

        &::before {
            content: '';
            position: absolute;
            top: 0;
            bottom: 0;
            left: 40px;
            height: 100%;
            width: 2px;
            background: #ffffff55; }

        .city {
            position: relative;
            color: #ffffff;
            padding: 20px 60px;
            cursor: pointer;

            &::before {
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                height: 100%;
                width: 200px;
                background: url('/decorations/inactive-city.png') no-repeat center;
                background-position-x: 21px; }

            &--active {
                background: url('/decorations/active-city.png') no-repeat center left / cover;
                cursor: initial; } } }

    .character {
        position: relative;
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: baseline;
        padding-top: 20vh;
        padding-left: 70px;

        &__emblem {
            position: absolute;
            top: 10vh;
            left: 0;
            width: 350px;
            opacity: 0.1;
            z-index: 4;
            transition: opacity 0.3s ease; }

        &__dash {
            z-index: 5;
            margin-bottom: 20px;
            height: 5px;
            width: 150px;
            background: #cda574; }

        &__name {
            z-index: 5;
            height: 120px; }



        $cvHeight: 55px;
        &__cv {
            z-index: 5;
            height: $cvHeight;
            display: flex;
            flex-direction: row;
            align-items: stretch;
            box-shadow: 0px 2px 3px #00000044;

            &__icon {
                height: 100%; }

            &__name {
                background: #cda574;
                color: #233837;
                display: flex;
                flex-direction: row;
                align-items: center;
                padding: 0 20px;
                font-size: 1.4em;
                font-weight: 500;
                min-width: 200px;
                max-width: 500px; }

            &__play {
                background-repeat: no-repeat;
                background-position: center;
                background-size: cover;
                border: 0;
                padding: 0;
                margin: 0;
                background-color: transparent;
                width: $cvHeight;
                cursor: pointer; } }

        &__description {
            z-index: 5;
            position: relative;
            background: #00000088;
            padding: 10px 5px;
            width: 70%;
            min-width: 300px;
            max-width: 550px;
            margin-top: 20px;
            height: 10vh;
            min-height: 120px;

            &::before {
                content: '';
                position: absolute;
                top: 15px;
                left: calc(50px / 2 - 20px / 2);
                width: 20px;
                height: 20px;
                background: url('/decorations/char-desc-emblem.png') no-repeat center center / cover; }

            &__text {
                position: relative;
                color: #fff;
                padding-left: 50px;
                height: 100%;
                overflow-y: auto;
                line-height: 1.6em;
                word-spacing: 3px; } }

        &__slogan {
            z-index: 5;
            margin-left: 250px;
            margin-top: 50px; } }

    .characters {
        z-index: 5;
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;

        &__wrapper {
            position: relative;
            padding: 20px 0;
            box-sizing: border-box;
            width: 100%;
            max-width: 100%; }

        &__bg {
            z-index: 2;
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            height: 20vh;
            width: 100%;
            background: linear-gradient(180deg, #ffffff33, #00000033); }

        &__list {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center; }

        &__slider-left,
        &__slider-right {
            height: 50px;
            width: 50px;
            border: 0;
            background-color: transparent;
            cursor: pointer;

            &:disabled {
                cursor: not-allowed; } }

        &__slider-left {
            background: url('/decorations/char-swiper-left.png') no-repeat center center / cover; }

        &__slider-right {
            background: url('/decorations/char-swiper-right.png') no-repeat center center / cover; }

        &__item {
            display: flex;
            flex-direction: column;
            align-items: center;
            background: #00000088;
            color: #fff;
            border-radius: 4px;
            overflow: hidden;
            margin: 0 20px;
            cursor: pointer;

            &--active,
            &:hover {
                background: #fff;
                color: #111; } }

        &__avatar {
            width: 100px;
            height: 100px;
            object-fit: cover; }

        &__item--active .characters__avatar, &__item:hover .characters__avatar {
            background: linear-gradient(135deg, #000000ee, #000000aa) !important; }

        &__name {} }

    $animationDuration: 1s;
    .fade-in {
        animation: fade-in $animationDuration; }

    .fade-in-out {
        animation: fade-in-out $animationDuration; }

    .swipe-in {
        animation: swipe-in $animationDuration; }

    .swipe-fade-in {
        animation: swipe-fade-in $animationDuration; }

    .fade-out {
        animation: fade-out $animationDuration forwards; }

    .emblem-fade-out {
        animation: emblem-fade-out $animationDuration forwards; }

    .swipe-out {
        animation: swipe-out $animationDuration; }

    .swipe-fade-out {
        animation: swipe-fade-out $animationDuration forwards; } }

@keyframes fade-in {
    0% {
        opacity: 0; }
    100% {
        opacity: 1; } }

@keyframes fade-in-out {
    0% {
        opacity: 0; }
    50% {
        opacity: 0.3; }
    100% {
        opacity: 0.1; } }

@keyframes swipe-in {
    0% {
        transform: translateX(200px); }
    100% {
        transform: translateX(0); } }

@keyframes swipe-fade-in {
    0% {
        transform: translateX(200px);
        opacity: 0; }
    100% {
        transform: translateX(0);
        opacity: 1; } }

@keyframes fade-out {
    0% {
        opacity: 1; }
    100% {
        opacity: 0; } }

@keyframes emblem-fade-out {
    0% {
        opacity: 0.1; }
    100% {
        opacity: 0; } }

@keyframes swipe-out {
    0% {
        transform: translateX(0); }
    100% {
        transform: translateX(200px); } }

@keyframes swipe-fade-out {
    0% {
        transform: translateX(0);
        opacity: 1; }
    100% {
        transform: translateX(200px);
        opacity: 0; } }
