.news {
    background: url('/news-bg.jpg') no-repeat center center;
    background-size: cover;
    min-height: 100vh;
    padding: 40px 0;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    &__section-title {
        position: relative;
        color: #ffffff;
        font-size: 3em;
        font-weight: 300;
        letter-spacing: 2px;

        $frillHeight: 15px;
        $halfFrillHeight: $frillHeight / 2;
        &::before {
            content: '';
            position: absolute;
            top: calc(50% - #{$halfFrillHeight});
            right: calc(100% + 50px);
            background: url('/decorations/frill.png') no-repeat right center/cover;
            width: 200px;
            height: $frillHeight; }
        &::after {
            content: '';
            position: absolute;
            top: calc(50% - #{$halfFrillHeight});
            left: calc(100% + 50px);
            background: url('/decorations/frill.png') no-repeat right center/cover;
            width: 200px;
            height: $frillHeight;
            transform: rotateZ(180deg); } }

    &__section-content {
        display: flex;
        flex-direction: row;
        align-items: stretch;
        justify-content: center;
        width: 80%;
        min-width: 90%;
        margin-top: 40px; }

    &__carousel {
        flex: 1; }

    &__latest {
        flex: 1;
        padding: 20px 30px;
        background: #00000077; }

    $borderColor: #ffffff33;
    &__type {
        display: flex;
        flex-direction: row;
        border-bottom: 4px solid $borderColor;

        &-btn {
            color: #ffd49f;
            border-bottom: 4px solid #ffd49f;
            padding: 15px;
            margin-bottom: -4px;
            font-size: 1.1em;
            background: transparent;
            cursor: pointer;
            transition: all 0.3s ease;

            &:hover {
 } } }                // background: #ffffff33

    &__list {
        list-style-type: none; }

    &__item {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        padding: 16px 10px;
        border-bottom: 2px solid $borderColor;
        background: transparent;
        cursor: pointer;
        transition: all 0.3s ease;

        &:hover {
            background: #ffffff22; } }

    &__title {
        color: #ffd49f;
        filter: saturate(0.2) brightness(1.2); }

    &__date {
        color: $borderColor;
        filter: brightness(1.1); }

    &__more {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;
        padding-top: 20px;

        &-btn {
            border: 1px solid $borderColor;
            padding: 7px 15px;
            color: #ffffff;
            font-size: 1em;
            background: transparent;
            cursor: pointer;
            transition: all 0.3s ease;

            &::before {
                content: '+';
                padding: 2px;
                margin-right: 10px;
                color: #ffd49f;
                font-size: 1em; }

            &:hover {
                color: #ffd49f;
                background: transparentize(#ffd49f, 0.7); } } } }

