.carousel {
    position: relative;
    height: 100%;
    width: 100%;
    overflow-x: hidden;

    &__images {
        height: 100%;
        width: 100%; }

    &__image {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        height: 100%;
        width: 100%;
        object-fit: cover;
        object-position: center center;

        &--previous {
            animation: leave 0.5s ease-in-out;
            z-index: 2;
            z-index: 2; }

        &--visible {
            animation: enter 0.5s ease;
            z-index: 3; } }

    &__controls {
        position: absolute;
        bottom: 20px;
        left: 0;
        right: 0;
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        z-index: 4; }

    $selectorColor: #eaeaea;
    &__selector {
        border: 2px solid $selectorColor;
        border-radius: 50%;
        background: transparent;
        height: 20px;
        width: 20px;
        margin: 0 10px;
        cursor: pointer;

        &--active {
            background: $selectorColor; } } }


@keyframes leave {
    0% {
        transform: translateX(0); }
    100% {
        transform: translateX(-100%); } }

@keyframes enter {
    0% {
        transform: translateX(100%); }
    100% {
        transform: translateX(0); } }
