.header {
    display: flex;
    flex-direction: row;
    align-items: center;
    position: fixed;
    top: 0;
    width: 100%;
    max-width: 100%;
    padding: 0 60px;
    box-sizing: border-box;
    background: #000000bb;
    z-index: 10;

    &__music {
        border: 0;
        background-color: transparent;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        width: 30px;
        height: 30px;
        cursor: pointer; }

    &__logo {
        height: 65px;
        width: 250px;
        object-fit: cover; }

    &__nav {
        align-self: stretch;
        display: flex;
        flex-direction: row;
        align-items: stretch; }

    &__link {
        padding: 22px 0;
        color: #f1f1f1;
        font-size: 1em;
        font-weight: 300;
        text-transform: uppercase;
        text-decoration: none;
        margin: 0 14px;
        display: flex;
        align-items: center;
        justify-content: center;
        box-sizing: border-box;

        &--active {
            text-shadow: 0 0 40px #16edf5, 0 0 10px #16edf5, 0 0 4px #16edf5;
            border-top: 5px solid #16edf5;
            padding-top: 17px; }

        &:not(&--active):hover {
            text-shadow: 0 0 40px #ffd49f, 0 0 10px #ffd49f, 0 0 4px #ffd49f;
            border-top: 2px solid #ffd49f;
            padding-top: 20px; } }

    &__login {
        margin-left: auto;
        display: flex;
        flex-direction: row;
        align-items: center;
        padding: 20px;
        color: #aaaaaa;

        &-icon {
            margin-left: 10px;
            font-size: 1.5em; } } }

